<template>
    <div :class="['content',type,{'c-alone':standalone}]">
        <slot></slot>
    </div>
</template>

<script>
    import {DJUtils} from '../assets/js/common'

    export default {
        props: {
            type: {type: String, default: ''}
        },
        ready(){
            this.standalone = DJUtils.isStandalone();
        },
        data(){
            return {
                standalone: false
            }
        },
    }
</script>
<style scoped>
    .content {
        position: absolute;
        overflow: scroll;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        left: 0;
        right: 0;
        top: .44rem;
        bottom: .52rem;
        padding-bottom: .13rem;
    }

    .content.c-alone {
        top: .84rem;
    }

    .content.list {
        top: 0;
        bottom: 0;
    }
    
    .content.form {
        top: .44rem;
        bottom: .52rem;
        padding-bottom: .13rem;
    }

    .content.form.c-alone {
        top: .84rem;
    }

    .content.none {
        bottom: 0;
    }

    .content.task-none {
        top: 1.5rem;
    }

    .content.task-none.c-alone {
        top: 1.7rem;
    }

    .content.task-detail {
        top: 1.19rem;
        bottom: .8rem;
        background-color: #fff;
    }

    .content.task-detail.c-alone {
        top: 1.59rem;
    }

    .content.login {
        left: .15rem;
        right: .15rem;
        bottom: 0;
    }

    .content.choose {
        bottom: .1rem;
    }

    .content.user-center {
        top: 1.6rem;
        bottom: .52rem;
    }

    .content.user-center.c-alone {
        top: 2rem;
    }

    .content.wallet {
        top: 1.85rem;
        bottom: 0;
    }

    .content.wallet.c-alone {
        top: 2.25rem;
    }
</style>



