<template>
  <div>
    <div class="section">
      <div class="matter">
        <p class="title">技能类别</p>
        <div class="edit" v-link="{name:'menu-all',query:{from:from,type:type}}">
          <p class="text none" v-if="service.name.length == 0">请选择技能类别（必选）</p>
          <p class="text" v-else>{{service.name}}</p>
          <span class="img transfer"></span>
        </div>
      </div>
      <div class="matter">
        <p class="title">技能名称</p>
        <div class="edit">
          <input type="text" placeholder="请输入技能名称（必填）" v-model="name" class="publish-lis name" maxlength="11"/>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">技能售价</p>
        <div class="edit">
          <input type="text" placeholder="请输入售价（必填）" v-model="price"
                  onkeyup="value=value.replace(/[^\d]/g,'')" maxlength="6" class="publish-lis price"/>
          <span class="unit">元/小时</span>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">服务日期</p>
        <div class="edit" v-link="{name:'choose-date',query:{from:from,type:type,targetType:1}}">
          <p class="text none" v-if="workDay.startTime.length == 0 && !workDay.timeless">请选择服务日期（必选）</p>
          <p class="text ellipsis" v-else>
            <span v-if="!workDay.timeless && workDay.startTime == workDay.endTime">{{workDay.startTime}}</span>
            <span v-if="!workDay.timeless && workDay.startTime != workDay.endTime">{{workDay.startTime}}-{{workDay.endTime}}</span>
            <span v-if="workDay.timeless">长期有效</span>

            <span v-if="workDay.startTime.length > 0 || workDay.timeless">({{workDay.split | week-format}})</span>
          </p>
          <span class="img transfer"></span>
        </div>
      </div>
      <div class="matter">
        <p class="title">服务时间</p>
        <div class="edit" v-link="{name:'choose-time',query:{from:from,type:type,targetType:1}}">
          <p class="text none" v-if="workTime.length == 0 ">请选择服务时间（必选）</p>
          <p class="text ellipsis" v-else>{{workTime}}</p>
          <span class="img transfer"></span>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">服务范围</p>
        <div class="edit" v-link="{name:'choose-area',query:{from:from,type:type}}">
          <p class="text none" v-if="workArea.length == 0 ">请选择服务范围（必选）</p>
          <p class="text" v-else>{{workArea | area-filter}}</p>
          <span class="img transfer"></span>
        </div>
      </div>
      <div class="matter">
        <p class="title">服务地址</p>
        <div class="edit" v-link="{name:'choose-address',query:{from:from,type:type,targetType:1}}">
          <p class="text none" v-if="address.length == 0 ">请填写详细服务地址</p>
          <p class="text ellipsis" v-else>{{address}}</p>
          <span class="img transfer"></span>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">技能介绍</p>
      </div>
      <div class="matter more">
        <textarea placeholder="请填写20~500字技能介绍（必填）" rows=4 v-model="intro" class="edit-area publish-lis intro"></textarea>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">服务说明</p>
      </div>
      <div class="matter more">
        <textarea placeholder="服务特殊说明" v-model="description" rows=4 class="edit-area publish-lis desc"></textarea>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import {toast} from '../assets/js/toast'
  import {url, st, publish, obj} from '../const'
  import {DJUtils} from '../assets/js/common'

  export default{
    props: {
      link_name: {type: String, default: 'add'}
    },
    ready(){
      let vm = this;
      vm.$on('reload-child',function(){
        let link = vm.link_name;
        if (link == 'add') {
          vm.type = 1;
          if (!st.has(st.keys.publishAdd)) {
            st.set(st.keys.publishAdd, obj.publish);
          }
          vm.pushEdit(obj.publish);
          vm.backEnd = this.dataToStr();
          vm.pushEdit(publish.get(this.type));
          vm.notify();
        } else if (link == 'edit') {
          vm.type = 2;
          let news = publish.get(vm.type);
          let query = vm.$route.params;
          //修改的发布和请求的发布一样时 直接拿本地的发布
          if (news && news.id == query.id && news.getTime + 1800 >= parseInt(new Date().getTime() / 1000)) {
            vm.pushEdit(news);
            vm.notify();
          } else {
            vm.getPublish(query.id);
          }
        }
      });
      vm.$emit('reload-child');

      $(".publish-lis").blur(function () {
        let _this = $(this);
        if (_this.is('.name') && !vm.name) {
          toast.alert('技能名称不能为空');
        }
        if (_this.is('.price') && (!vm.price || !/^[0-9]*[1-9][0-9]*$/.test(vm.price))) {
          toast.alert('技能售价必须大于0且不能有小数');
        }

        if (_this.is('.intro') && (!vm.intro || (vm.intro.length < 20 || vm.intro.length > 500))) {
          toast.alert('技能介绍必须填写20-500字');
        }
        if (_this.is('.desc') && (!vm.intro || (vm.description.length > 500))) {
          toast.alert('服务说明必须小于500字');
        }
        vm.notify();
      });
      $(".publish-lis").keyup(function () {
        vm.notify();
      });
      vm.from = vm.$route.path;
      
      //注册事件
      vm.$on('destroy-publish', vm.destroyPublish);
      vm.$on('publish', vm.doPublish);
      vm.$on('cancel-publish', vm.cancelPublish);
      vm.$on('save-publish', vm.savePublish);
      vm.$on('back-tip', function(){
         if(this.type==1){
          let backEnd = this.dataToStr();
          if(backEnd!=vm.backEnd){
            toast.conform('数据已修改,确定退出吗？',function(){
              DJUtils.backHistory();
            },true,null,{do:'退出',cancel:'取消'});
          }else{
            DJUtils.backHistory();
          }
        }else{
          let dataStr = vm.dataToStr();
          let backEnd = publish.get(this.type).backEnd;
          if(dataStr == backEnd){
            DJUtils.backHistory();
          }else{
            toast.conform('数据已修改,确定退出吗？',function(){
            DJUtils.backHistory();
            },true,null,{do:'退出',cancel:'取消'});
          }
        }
      });
    }
    ,
    data()
    {
      return {
        id: 0,
        service: {name: '', serviceId: 0},
        name: '',
        price: '',
        workDay: {startTime: '', endTime: '', split: ''},
        workTime: '',
        workArea: '',
        intro: '',
        description: '',
        address: '',
        status: 0,
        //跳转
        from: '',
        type: 0,
        isEdit:false,
        backEnd:''
      }
    },
    methods: {
      pushEdit(news){
        this.id = news.id;
        this.service = news.service;
        this.name = news.name;
        this.price = news.price;
        this.workDay = news.workDay;
        this.workTime = news.workTime;
        this.workArea = news.workArea;
        this.intro = news.intro;
        this.description = news.description;
        this.address = news.address;
        this.status = news.status;
      },
      checkEdit(){
        if (!this.service.name) {
          return false;
        } else if (!this.name) {
          return false;
        } else if (!this.price || !/^[0-9]*[1-9][0-9]*$/.test(this.price)) {
          return false;
        } else if (!this.workDay.startTime && !this.workDay.timeless) {
          return false;
        } else if (!this.workTime) {
          return false;
        } else if (!this.intro || (this.intro.length < 20 || this.intro.length > 500)) {
          return false;
        }
        return true;
      },
      notify(){
        this.isEdit = this.checkEdit();
        this.$dispatch('edit-status', this.isEdit);
        let news = publish.get(this.type);
        news.name = this.name;
        news.price = this.price;
        news.intro = this.intro;
        news.description = this.description;
        publish.set(news, this.type);
      },
      savePublish(){
        let vm = this;
        if(!vm.isEdit){
          return;
        }

        if (this.link_name == 'add') {
          vm.$http.get(url.addJob, vm.editData(0)).then(
            function ({data}) {
              if (data.success) {
                toast.alert('保存成功');
                publish.remove(vm.type);//清空缓存
                //返回待发布
                window.location.href = "#/publish?status=" + 0;
               /* window.history.back();*/
              } else {
                toast.alert(data.resultMsg);
              }
            }
          )
        }

        if (this.link_name == 'edit') {
          let dataStr = vm.dataToStr();
          let backEnd = publish.get(this.type).backEnd;
       
          if(dataStr == backEnd){
            toast.alert('数据无变化');
            return;
          }
          vm.$http.get(url.updateJob, vm.editData(vm.status)).then(
            function ({data}) {
              if (data.success) {
                toast.alert('保存成功');
                publish.remove(vm.type);//清空缓存
                window.history.back();
              } else {
                toast.alert(data.resultMsg);
              }
            }
          )
        }
      },
      doPublish(){
        let vm = this;
        if (this.link_name == 'add') {
          //新建界面的发布是保存并发布，转态传1
          vm.$http.get(url.addJob, vm.editData(1)).then(
            function ({data}) {
              if (data.success) {
                toast.alert('发布成功');
                publish.remove(vm.type);//清空缓存

                //返回发布中
                window.location.href = "#/publish?status=" + 1;
                /*window.history.back();*/
              } else {
                toast.alert(data.resultMsg);
              }
            }
          )
        }
        if (this.link_name == 'show') {
          console.log('show ...');
          vm.$http.get(url.changeStatus, {id: this.id, status: 1}).then(
            function ({data}) {
              if (data.success) {
                toast.alert('发布成功');
                vm.$dispatch('publish-status', 1);
              } else {
                toast.alert(data.resultMsg);
              }
            }
          )
        }
      },
      cancelPublish(){
        let vm = this;
        toast.conform('确定取消?', function () {
          vm.$http.get(url.changeStatus, {id: vm.id, status: 0}).then(
            function ({data}) {
              if (data && data.success) {
                toast.alert('取消成功');
                vm.$dispatch('publish-status', 0);
              } else {
                toast.alert('系统异常，请联系管理员');
              }
            }
          )
        });
      },
      destroyPublish(){
        let vm = this;
        toast.conform('确定删除?', function () {
          vm.$http.get(url.changeStatus, {id: vm.id, status: 3}).then(
            function ({data}) {
              if (data && data.success) {
                toast.alert('删除成功');
                window.history.back();
              } else {
                toast.alert('系统异常，请联系管理员');
              }
            }
          )
        });
      },
      getPublish(id){
        toast.loading();
        this.$http.get(url.publishDetail, {id: id}).then(
          function ({data:{success, data, resultMsg, code}}) {
            if (success) {
              let name = data.subCategoryName;
              let serviceId = data.subCategory;
              let str = serviceId + "";
              if (str.substring((str.length - 2), str.length) == "99") {
                name = data.categoryName + '-其他';
              }
              let p = {
                id: data.id,
                service: {serviceId: serviceId, name: name},
                name: data.name,
                price: data.price,
                workDay: {startTime: data.startTime, endTime: data.endTime, split: data.workSplit,timeless:data.timeless},
                workTime: data.workTime,
                workArea: data.workArea,
                intro: data.intro,
                description: data.description,
                address: data.address,
                status: data.status,
                getTime: parseInt(new Date().getTime() / 1000)
              };
              this.pushEdit(p);
              this.$dispatch('publish-status', this.status);//发送状态
              p.backEnd = this.dataToStr();
              publish.set(p, this.type);
              this.notify();
            } else {
              toast.alert(resultMsg);
            }
          }, function (response) {
            toast.alert('网络异常');
          }
        )
      },
      publishVaild(){
        this.$http.get(url.publishVaild).then(
          function({data:{success,data}}){
              if(success){
                let vm = this;
                if(!data.publishVaild){
                  toast.conform('请先完善个人信息，再发布任务',function(){
                    window.history.back();
                  },true,function(){
                    window.location.href='#/user/info?from='+vm.$route.path;
                  },{do:'返回',cancel:'去完善'});
                }
              }
          });
      },
      dataToStr(){
        let vm = this;
        let data = {
          id: this.id,
          subCategory: vm.service.serviceId,
          name: vm.name,
          price: vm.price,
          startTime: vm.workDay.startTime,
          endTime: vm.workDay.endTime,
          workTime: vm.workTime,
          validWeek: vm.workDay.split,
          workArea: vm.workArea,
          intro: vm.intro,
          description: vm.description,
          address: vm.address
        };
        let backEnd = '';
        for(let i in data){
          backEnd += (data[i]+'');
        }
        return backEnd;
      },
      editData(status){
        let vm = this;
        let postData = {
          id: this.id,
          subCategory: vm.service.serviceId,
          name: vm.name,
          price: vm.price,
          startTime: vm.workDay.startTime,
          endTime: vm.workDay.endTime,
          timeless: vm.workDay.timeless,
          workTime: vm.workTime,
          validWeek: vm.workDay.split,
          workArea: vm.workArea,
          intro: vm.intro,
          description: vm.description,
          address: vm.address,
          status: status
        }
        return postData;
      }
    },
    computed: {}
  }
</script>
<style scoped>
  .section .matter {
    font-size: .14rem;
  }

  .matter.more {
    height: auto;
  }

  .edit .unit {
    position: absolute;
    right: .1rem;
    top: 0;
    color: #ffb600;
  }

  .matter .edit-area,.edit.more {
    line-height: 1.4;
    width: 100%;
    margin-top: .1rem;
    padding-right: .1rem;
  }

  .edit .text {
    color: #333;
  }

  .edit .text.none {
    color: #999;
  }

  .edit input[type='text'] {
    width: 100%;
    text-indent: 0;
    padding: 0;
  }
</style>



