<template>
  <div>
    <v-header>
      <div slot="right" :class="['h-right',isEdit ? '' : 'opa-6']"><a @click="save" class="mg-r10">保存</a></div>
    </v-header>

    <v-content :type="'choose'">
      <v-publish-detail class="publish-detail" :link_name="'edit'">

      </v-publish-detail>
    </v-content>

    <!--<v-footer-btn v-show="status != 2">-->
    <!--<input type="button" value="废弃" :class="['input','small','destroy']" v-if="status == 0"/>-->
    <!--<input type="button" value="发布" :class="['input','mid','right',isEdit ? 'ok' : 'nk']" v-if="status == 0"/>-->
    <!--<input type="button" value="取消发布" :class="['input','max','cancel']" v-if="status == 1"/> -->
    <!--</v-footer-btn>-->
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  //  import VFooterBtn from '../../components/FooterBtn'
  import VPublishDetail from '../../components/PublishDetail'

  export default{
    route:{    
      data({from,to}){
        if(this.isReady){
          return {
            state:!this.state
          }
        }    
      }
    },    
    ready(){
      this.isReady = true;
    },
    data(){
      return {
        isReady:false,
        state:false,     
        
        isEdit: false,
        status: 0
      }
    },
    methods: {
      save(){
        if (this.isEdit) {
          this.$broadcast('save-publish');
        };
      }
    },
    events: {
      'edit-status': function (isEdit) {
        this.isEdit = isEdit;
      },
      'publish-status': function (status) {
        this.status = status;
      },
      'backtip':function(){
        this.$broadcast('back-tip');
      }
    },
    watch:{
      'state':function(){
        this.isEdit = false;
        this.status = 0;
        this.$broadcast('reload-child');
      }
    },
    components: {VHeader, VContent, /* VFooterBtn, */VPublishDetail}
  }
</script>





