<template>
    <div>
        <div v-if="standalone" class="standalone z-index-1"></div>
        <div :class="['my-header','z-index-1',{'alone':standalone}]">
            <div class="warp">
                <slot name="left">
                    <div class="h-left">
                        <a @click="backHistory()" v-if="!$route.back && !$route.backtip"><span class="img back"></span></a>
                        <a v-link="{name:$route.back}" v-if="$route.back"><span class="img back"></span></a>
                        <a @click="backtip" v-if="$route.backtip"><span class="img back"></span></a>
                    </div>
                </slot>
                <slot name="title">
                    <div class="title">{{$route.title}}</div>
                </slot>
                <slot name="right"></slot>
            </div>
        </div>
    </div>
</template>

<script>
    import {url, lt} from '../const'
    import {DJUtils} from '../assets/js/common'
    export default{
        ready(){
            this.$on('login-check', function () {
                if (lt.has("user")) {
                    this.$http.get(url.loginCheck).then(
                        function ({data:{success, data}}) {
                            if (success) {
                                this.$dispatch('is-login', data.isLogin);
                                if (!data.isLogin) {
                                    console.log('用户未登陆');
                                    lt.remove("user");
                                }
                            }
                        });
                } else {
                    this.$dispatch('is-login', false);
                }
            });
            this.standalone = DJUtils.isStandalone();
        },
        data(){
            return {
                standalone: false
            }
        },
        methods: {
            backtip(){
                this.$dispatch('backtip');
            },
            backHistory(){
                DJUtils.backHistory();
            }
        }
    }
</script>

<style>
    .standalone {
        position: fixed;
        height: .42rem;
        background-color: #00cda9;
        top: 0;
        left: 0;
        right: 0;
    }

    .my-header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: .44rem;
        line-height: .44rem;
        color: #fff;
        font-size: .18rem;
        text-align: center;
    }

    .my-header.alone {
        top: .4rem;
    }

    .my-header .warp {
        height: 100%;
        background-color: #00cda9;
    }

    .replace .standalone, .replace .my-header .warp {
        background-color: #9e9e9e;
    }

    .my-header a {
        display: block;
        color: #fff;
        width: .44rem;
        height: .44rem;
    }

    .my-header .title {
        margin: 0 auto;
    }

    .my-header .img {
        width: .22rem;
        height: .22rem;
        background-size: .22rem;
        display: block;
        margin: .1rem auto;
        position: absolute;
    }

    .h-left .img {
        left: .1rem;
    }

    .h-right .img {
        right: .1rem;
    }

    .img.back {
        height: .22rem;
        width: .12rem;
        background-size: .12rem;
        background-image: url(../assets/img/top/arrow02.png);
    }

    .img.help {
        background-image: url(../assets/img/top/help.png);
    }

    .my-header .h-left {
        position: absolute;
    }

    .my-header .h-right {
        position: absolute;
        right: 0;
        top: 0;
    }

    /*search*/
    .my-header .search {
        height: .4rem;
        padding-bottom: .1rem;
        margin: 0 .35rem;
        position: relative;
        font-size: .12rem;
        color: #c0c0c0;
    }

    .search .img {
        position: absolute;
        left: .1rem;
        top: .03rem;
        width: .15rem;
        height: .15rem;
        background-size: .15rem;
        background-image: url(../assets/img/icon/search.png);
    }

    .search .keyword {
        text-indent: .3rem;
        width: 100%;
        height: 100%;
        border-radius: .03rem;
        text-align: left;
    }
</style>



